body {
  position: relative;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin: 2rem;
  padding: 1rem 4rem 0 2rem;
  position: absolute;
}

#hir {
  background-color: black;
  color: white;
  padding: 10px 90px 10px 20px;
  border: none;
}

.header .h1 {
  margin-right: 10%;
}

.ourServices {
  height: 100vh;
  width: 100vw;
}

.binder {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  height: 100%;
}

.body {
  display: flex;
  justify-content: space-between;
  padding: 5rem;
  width: 82%;
  height: 40%;
  align-items: center;
  margin-left: 5%;
  padding: 2%;
}

.body2 {
  display: flex;
  width: 80%;
  justify-content: space-between;
  height: 100%;
}

.b1 {
  height: 90%;
  margin: 1%;
}

.b1 ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  list-style-type: none;
  height: 100%;
  padding: 0;
}

.b1 ul li {
  width: 10px;
  height: 10px;
  border: 1.5px solid black;
}

.b1 ul li.active {
  background-color: black;
}

.b2,
.b3 {
  width: 50%;
}

.b21 {
  width: 70%;
}

.b2 p {
  font-size: 10px;
  line-height: 1.5rem;
  font-weight: bold;
}

.b2 {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.b2 button {
  font-size: 10px;
  background-color: black;
  color: white;
  border: none;
}

.b3 ul {
  line-height: 2.5rem;
  font-size: 10px;
  font-weight: bold;
  padding: 0%;
}

@media (max-width: 1200px) {
  .header {
    width: 50%;
  }
  .header img {
    height: 50px;
  }
  .header h1 {
    font-size: 1rem;
  }
  .body .b1 {
    display: none;
  }
  .body2 {
    flex-direction: column;
    justify-content: center;
  }
  .body {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .b2,
  .b3 {
    width: 100%;
    text-align: center;
  }
  .b21 {
    width: 100%;
  }
}

@media (max-width: 700px) {
  .header {
    width: 50%;
  }
  .header img {
    height: 30px;
    margin-right: 1rem;
  }
  .header h1 {
    font-size: 10px;
    margin-right: 1rem;
  }
  #hir {
    background-color: black;
    color: white;
    padding: 2px 20px;
    border: none;
    font-size: 10px;
  }
}
